body {
  background: linear-gradient(-45deg, #f3c7b9, #1c1c1c, #0e305c, #c9eee5);
  background-size: 400% 400%;
  -webkit-animation: Gradient 15s ease infinite;
  -moz-animation: Gradient 15s ease infinite;
  animation: Gradient 15s ease infinite;
  font-family: "futura-demic-reg", Arial, sans-serif;
}

@-webkit-keyframes Gradient {
  0% {
    background-position: 0% 50%;
  }

  50% {
    background-position: 100% 50%;
  }

  100% {
    background-position: 0% 50%;
  }
}

@-moz-keyframes Gradient {
  0% {
    background-position: 0% 50%;
  }

  50% {
    background-position: 100% 50%;
  }

  100% {
    background-position: 0% 50%;
  }
}

@keyframes Gradient {
  0% {
    background-position: 0% 50%;
  }

  50% {
    background-position: 100% 50%;
  }

  100% {
    background-position: 0% 50%;
  }
}

.board {
  box-sizing: border-box;
  width: 40%;
  padding-top: 40%;
  height: 0;
  border: 1px solid #acacac;
  position: relative;
  background: #acacac;
  border-radius: 2%;
}

.game h1 {
  color: #dfd6c5;
  font-size: 5em;
  text-align: center;
}

.chip {
  box-sizing: border-box;
  width: 25%;
  padding-top: 25%;
  height: 0;
  border: 1px solid #777;
  position: absolute;
  top: 0;
  left: 0;
  background-color: #333333;
  color: blanchedalmond;
  border-radius: 6%;
  transition: all .6s ease-in;
}

.chip__text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 4em;
}

.chip--moved {
  cursor: pointer;
  background-color: #a0a0a0;
}

.wrap {
  max-width: 1180px;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
  z-index: 1;
}

.boardWrap {
  display: flex;
  justify-content: space-around;
}

.data {
  box-sizing: border-box;
  width: 40%;
  padding: 0;
}

.modal {
  display: block;
  position: fixed;
  z-index: 5;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal-content {
  margin: 15% auto;
  border: 1px solid gray;
  width: 50%;
  height: 5%;
  border: none;
  text-align: center;
}

::placeholder {
  color: white;
  font-size: 1.5em;
}

#nameModal {
  width: 59%;
  height: 48px;
  border-left: 1px solid #9efdc7 !important;
  border: none;
  background-color: black;
  color: white;
  font-size: 1.5em;
  outline: none;
  padding-left: 10px;
}

#enter {
  width: 50px;
  height: 50px;
  border-radius: 50px;
  text-align: center;
  border: 2px solid #9efdc7;
  background: black;
  color: #9efdc7;
  font-size: 1.5em;
  font-weight: bold;
  outline: none;
}

.youName {
  color: #fcdfb5;
  font-size: 2em;
}

.numbers {
  color: #fcdfb5;
  font-size: 2em;
}

.data p {
  color: blanchedalmond;
  margin: 0;
  font-size: 2em;
}

.step {
  color: #fcdfb5;
  font-size: 2em;
}

.step span {
  color: blanchedalmond;
  margin: 0;
  font-size: 2em;
}

.numberGame {
  color: #fcdfb5;
  font-size: 2em;
}

.numberGame span {
  color: blanchedalmond;
  margin: 0;
  font-size: 2em;
}

